@import "./global.scss";

.App {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  overflow: hidden;
  text-align: center;
  display: flex;
  justify-content: center;
  background-color: $backgroundColor;

  .websiteContent {
    width: 100%;
    max-width: 1200px;
    height: 100%;
    position: absolute;
    top: 0;
    overflow: hidden;
    text-align: center;
    background-color: orange;

    .noData {
      width: 100%;
      height: 100%;
      background-color: $backgroundColor;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      h1 {
        color: $textColor;
      }
    }

    .sections {
      width: 100%;
      height: 100%;
      min-height: 700px;
      //display: flex;
      //flex-direction: column;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 1s ease-out;
      background-color: white;
      //overflow: hidden;

      //position: absolute;
      //width: 100%;
      //height: 100%;
      //min-height: 700px;
      //background-color: white;
      //display: flex;
      //flex-direction: column;
      //align-items: flex-start;
      touch-action: none;

      scroll-behavior: auto;
      scroll-snap-type: y mandatory;
      scroll-snap-stop: always;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }

      .container {
        width: calc(100% - 20px);
        height: 100%;
        margin-left: 10px;
        position: absolute;
        padding-top: 60px;
        background-color: darkgreen;
        touch-action: none;
        //
        //width: calc(100% - 20px);
        //height: 100%;
        //margin-left: 10px;
        //position: relative;
        //padding-top: 60px;
        //background-color: darkgreen;
        //touch-action: none;
        transition: all 500ms;

        scroll-snap-align: start;
      }
    }
  }
}
